import React, { useEffect, useState } from "react"
import Helmet from "react-helmet"
import Layout from "../component/layout"
import ISSO from "../component/comment"
import { css } from "@emotion/core"
import mainLandFeatures from "../../static/features.json"
import Select from "react-select"

const hStyle = css`
  font-size: 1.6rem;
  text-align: center;
  margin: 1rem 0;
  @media (min-width: 500px) {
    font-size: 2rem;
    margin: 3rem 0;
  }
`

const formStyle = css`
  margin: 0 0 0.5rem 0;
  padding: 0;
  fieldset {
    border: 1px solid grey;
  }
  fieldset legend {
    background-color: black;
    color: white;
    padding: 4px 1rem;
    font-size: 0.9rem;
    font-weight: bold;
  }
  .systemSelector {
    display: flex;
    flex-wrap: wrap;
    p {
      display: flex;
      align-items: center;
      margin: 0.3rem 1rem 0.3rem 0;
    }
    p label {
      margin-left: 0.3rem;
    }
  }
`

const featuresStyle = css`
  margin-bottom: 1rem;
  h2 {
    font-size: 1.3rem;
    margin: 2rem 0;
  }
  .h2-country {
    color: white;
    border: 1px solid black;
    background: black;
    padding: 0.2rem 0.4rem;
  }
  .h2-device {
    border: 1px solid black;
    padding: 0.2rem 0.4rem;
  }
  .all-features {
    display: flex;
    flex-wrap: wrap;
    div {
        flex: 1;
    }
  }
`

const tipStyle = css`
  background: rgb(204, 246, 207);
  padding: 0.5rem;
  margin-top: 1rem;
  border-radius: 0.4rem;
  h2 {
    font-size: 0.9rem;
    margin: 0;
    color: rgb(44, 167, 41);
  }
  ul {
    list-style-position: outside;
    padding-left: 1rem;
    margin: 0;
  }
  ul li {
    font-size: 0.8rem;
    color: rgb(44, 167, 41);
  }
`

const Features = () => {
  const [country, setCountry] = useState("中国大陆")
  const [checked, setChecked] = useState(true)
  const [allCountriesFeatures, setAllCountriesFeatures] = useState()
  const [features, setFeatures] = useState()
  const [selectedOption, setSelectedOption] = useState()
  const [selectOptions, setSelectOptions] = useState([
    { value: "中国大陆", label: "中国大陆" },
    { value: "香港", label: "香港" },
    { value: "澳门", label: "澳门" },
    { value: "美国", label: "美国" },
  ])
  const [device, setDevices] = useState("iPhone/iPad")
  const devices = [
    { device: "iPhone/iPad", checked: { checked } },
    { device: "Apple Watch" },
    { device: "Mac" },
  ]

  useEffect(() => {
    const getAllCountriesFeatures = async () => {
      const alliPhoneFeatures = fetch(
        `https://api.kejiweixun.com/api/feature_iphone`
      ).then(res => res.json())
      const allWatchFeatures = fetch(
        `https://api.kejiweixun.com/api/feature_watch`
      ).then(res => res.json())
      const allMacFeatures = fetch(
        `https://api.kejiweixun.com/api/feature_mac`
      ).then(res => res.json())
      const allDeviceCountriesFeatures = await Promise.all([
        alliPhoneFeatures,
        allWatchFeatures,
        allMacFeatures,
      ])
      setAllCountriesFeatures({
        alliPhoneFeatures: allDeviceCountriesFeatures[0],
        allWatchFeatures: allDeviceCountriesFeatures[1],
        allMacFeatures: allDeviceCountriesFeatures[2],
      })
    }
    getAllCountriesFeatures()
  }, [])

  useEffect(() => {
    const deviceTweaked =
      device === "iPhone/iPad"
        ? "alliPhoneFeatures"
        : device === "Mac"
          ? "allMacFeatures"
          : "allWatchFeatures"
    if (allCountriesFeatures) {
      const getCountries = async () => {
        const countries = Object.keys(allCountriesFeatures[deviceTweaked]).map(
          item => {
            return {
              value: item,
              label: item,
            }
          }
        )
        setSelectOptions(countries)
      }
      getCountries()
    }
  }, [device, allCountriesFeatures])

  useEffect(() => {
    const deviceTweaked =
      device === "iPhone/iPad"
        ? "alliPhoneFeatures"
        : device === "Mac"
          ? "allMacFeatures"
          : "allWatchFeatures"
    if (allCountriesFeatures) {
      const getFeatures = async () => {
        if (allCountriesFeatures[deviceTweaked][country]) {
          setFeatures(allCountriesFeatures[deviceTweaked][country])
        } else {
          setFeatures({
            features: ["奇怪, 苹果官网没有该产品在该地区的功能可用性信息"],
            unavailable: ["奇怪, 苹果官网没有该产品在该地区的功能可用性信息"],
          })
        }
      }
      getFeatures()
    }
  }, [device, country, allCountriesFeatures])

  const inputEle = devices.map(device => {
    return (
      <p key={device.device}>
        <input
          type="radio"
          name="device"
          value={device.device}
          id={device.device}
          defaultChecked={device.checked}
        />
        <label htmlFor={device.device}>{device.device}</label>
      </p>
    )
  })

  const handleChange = e => {
    setDevices(e.target.value)
    if (Number(e.target.value) !== "iPhone/iPad") {
      setChecked(false)
    }
  }

  const handleSelectChange = selectedOption => {
    setCountry(selectedOption.value)
    setSelectedOption(selectedOption)
  }

  const featuresListEle = () => {
    const deviceFeatures = features ? features : mainLandFeatures[device]
    const featuresCount =
      deviceFeatures.features[0] ===
        "奇怪, 苹果官网没有该产品在该地区的功能可用性信息"
        ? 0
        : deviceFeatures.features.length
    const unavailableCount =
      deviceFeatures.unavailable[0] ===
        "奇怪, 苹果官网没有该产品在该地区的功能可用性信息"
        ? 0
        : deviceFeatures.unavailable.length
    return (
      <>
        <h2>
          <span className="h2-country">{country}</span>
          <span className="h2-device">{device}</span>
        </h2>
        <article className="all-features">
          <section>
            <h3>{`可用功能 (${featuresCount}个) :`}</h3>
            <ul>
              {deviceFeatures.features.map(item => {
                return <li key={item}>{item}</li>
              })}
            </ul>
          </section>
          <div />
          <section>
            <h3 className="unavailable-features">{`不可用功能 (${unavailableCount}个) :`}</h3>
            <ul>
              {deviceFeatures.unavailable.map(item => {
                return <li key={item}>{item}</li>
              })}
            </ul>
          </section>
        </article>
      </>
    )
  }

  return (
    <Layout>
      <Helmet>
        <title>iPhone, iPad, Watch, Mac 各国各地区功能可用性对比</title>
        <meta
          name="description"
          content="iOS, watchOS, macOS 功能可用性查询, 已经按国家或地区进行重新分类, 可查看 100 多个国家或地区的信息, 看看哪个版本的 iPHone, iPad, Mac, Apple Watch 功能最完整."
        />
      </Helmet>
      <header>
        <h1 css={hStyle}>苹果功能可用性对比</h1>
        <form css={formStyle} onChange={handleChange}>
          <fieldset>
            <legend>请选择设备:</legend>
            <div className="systemSelector">{inputEle}</div>
          </fieldset>
        </form>
        <Select
          value={selectedOption}
          onChange={handleSelectChange}
          options={selectOptions}
          placeholder="请选择国家或地区..."
          theme={theme => ({
            ...theme,
            borderRadius: 0,
            colors: {
              ...theme.colors,
              primary25: "neutral20",
              primary: "black",
            },
          })}
        />
      </header>
      <section css={tipStyle}>
        <h2>
          <span role="img" aria-label="star emoji">
            ✨
          </span>
          提示:
        </h2>
        <ul>
          <li>信息同步自苹果官网, 覆盖 100 多个国家或地区;</li>
          <li>这里对比的是 iOS watchOS macOS 的系统差异, 不是硬件差异;</li>
          <li>少数功能可能只适用于某些新机型, 例如"超宽频"只适用于11系列;</li>
          <li>少数功能可能只适用于一个国家中的某些城市或地区, 没有全国覆盖;</li>
        </ul>
      </section>
      <section css={featuresStyle}>{featuresListEle()}</section>
      <ISSO title="苹果系统功能可用性" slug="/apple-features-availability/" />
    </Layout >
  )
}

export default Features